import React from "react";
import { Routes, Route } from "react-router-dom";
import Home from "./Home";
import Kampanja from "./Kampanja";
import Tietoa from "./Tietoa";
import TopMenu from "./TopMenu";
import Footer from "./Footer";

/**
 * App.js mrittelee, miten sivu jakautuu eri osiin. Kytnnss jokaisella sivulla ensimmisen
 * elementtin on TopMenu, joka palautetaan TopMenu.js-tiedostossa. Tmn jlkeen React-routed-domin
 * Routes ja Route mahdollistavat sivustossa eri sivuilla liikkumisen. Kytnnss sivua vaihdetaan
 * pelkstn TopMenun kautta. Lopuksi jokaisen sivun viimeisen elementtin on Footer, joka palautetaan
 * taas Footer-tiedostossa.
 */

export default function App() {
  return (
      <div>
        <TopMenu />
        <Routes>
          <Route path="/" element={<Home />} />
          <Route path="/Kampanja" element={<Kampanja />} />
          <Route path="/Tietoa" element={<Tietoa />} />
        </Routes>
        <Footer />
      </div>
  );
}